.landing {
	/* background-color: #a4b4ff; */
	position: relative;
	background-image: url('./images/tree.png'),
		linear-gradient(90deg, #576085, #8b9adb);
	background-position: right 0 top -300px;
	width: 100%;
	height: 780px;
	overflow: hidden;
}

.name_tag_proLinks {
	position: relative;
	opacity: 0;
	z-index: 1000;
	filter: drop-shadow(-2px 3px 2px hsla(0, 0%, 0%, 0.3));
}

.landing .name {
	position: relative;
	font-family: 'title';
	font-size: calc(80px + 0.7vw);
	font-weight: 800;
	margin: 250px auto 0 auto;
	z-index: 1000;

	// text-shadow: -2px 4px 0 #3f3c3c;
	text-shadow: -3px 5px 3px rgba(0, 0, 0, 0.3);
}

.landing .tag {
	font-family: 'title';
	font-size: calc(30px + 0.5vw);
	font-weight: 200;
	margin: 0px auto 0 auto;
	text-shadow: -2px 3px 2px rgba(0, 0, 0, 0.3);
}

.proLinksLanding img {
	width: 40px;
	margin-left: 15px;
	margin-top: 10px;
	padding: 0px;
}

.iconRow img {
	padding: 1em 1em;
}

.iconRow .jsIcon {
	width: 50px;
}

.iconRow .reactIcon {
	width: 50px;
}

.iconRow .bigIcon {
	width: 40px;
}

.iconRow .smallIcon {
	width: 120px;
}

.iconRow {
	position: absolute;
	top: 0;
	left: 0;
	filter: drop-shadow(-3px 5px 3px hsla(0, 0%, 0%, 0.3));
}

.borderBlur {
	background-image: linear-gradient(
		120deg,
		rgba(56, 91, 25, 1),
		rgba(147, 187, 101, 1)
	);
	position: absolute;
	width: 100%;
	height: 40px;
	top: 760px;
	z-index: 1000;
	filter: blur(10px);
}

//WIND
.wind {
	// background-color: aqua;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 2;
}

svg {
	margin: 0px auto;
	position: absolute;
	height: 100%;
	width: 100%;
}

#wind1 {
	position: absolute;
	width: 500px;
	left: 5%;
	top: 250px;
	transform: rotate(10deg);
}

#wind5 {
	position: absolute;

	left: 60%;

	top: 130px;
	transform: rotate(-10deg);
}

#wind6 {
	position: absolute;
	height: fit-content;
	left: 35%;
	margin-top: -80px;
}

#wind1 path {
	stroke-dasharray: 800;
	stroke-dashoffset: 800;
	animation: dash 11s ease-in forwards infinite;

	@for $i from 1 through 2 {
		&:nth-child(#{$i}) {
			animation-delay: random(2) + ($i - 1) * 6 + 6s;
		}
	}
}

#wind5 path {
	stroke-dasharray: 800;
	stroke-dashoffset: 800;
	animation: dash 11s ease-in forwards infinite;

	@for $i from 1 through 2 {
		&:nth-child(#{$i}) {
			animation-delay: random(3) + ($i - 1) * 6 + 6s;
		}
	}
}

#wind6 path {
	stroke-dasharray: 800;
	stroke-dashoffset: 800;
	animation: dash 11s ease-in forwards infinite;

	@for $i from 1 through 2 {
		&:nth-child(#{$i}) {
			animation-delay: random(4) + ($i - 1) * 6 + 6s;
		}
	}
}

@keyframes dash {
	0% {
		stroke-dashoffset: 800;
	}
	10% {
		stroke-dasharray: 400;
		stroke-dashoffset: 0;
	}
	20%,
	100% {
		stroke-dasharray: 800;
		stroke-dashoffset: -800;
	}
}
